import React, { Component } from 'react';
import styles from './App.module.css';
import * as API from '../../services/api';
import ImageGallery from '../ImageGallery/ImageGallery';
import SearchBar from '../SearchBar/SearchBar';
import ImageGalleryItem from '../ImageGalleryItem/ImageGalleryItem';
import Loader from '../Loader/Loader';
import Button from '../Button/Button';
import Modal from '../Modal/Modal';

const mapper = items => {
  return items.map(
    ({
      webformatURL: smallImg,
      largeImageURL: largeImg,
      tags: alt,
      id: key,
      ...props
    }) => ({
      smallImg,
      largeImg,
      alt,
      key,
      ...props,
    }),
  );
};

class App extends Component {
  state = {
    items: [],
    isLoading: false,
    pageNumber: 1,
    searchQuery: '',
    isOpenModal: false,
    openImg: '',
  };

  componentDidMount() {
    const { searchQuery, pageNumber } = this.state;
    this.fetchArticles(searchQuery, pageNumber);
  }

  componentDidUpdate(prevProps, prevState) {
    const { searchQuery, pageNumber } = this.state;
    if (
      prevState.searchQuery !== searchQuery ||
      prevState.pageNumber !== pageNumber
    ) {
      this.fetchArticles(searchQuery, pageNumber);
    }
  }

  fetchArticles = (query, page) => {
    this.setState({ isLoading: true });

    API.getItems(query, page)
      .then(({ data }) => {
        this.setState(prevState => ({
          items: [...prevState.items, ...mapper(data.hits)],
        }));
      })
      .catch(err => {
        throw new Error(err);
      })
      .finally(() => this.setState({ isLoading: false }));
  };

  handleSearch = query => {
    this.setState({ searchQuery: query, pageNumber: 1, items: [] });
  };

  handleLoadMore = () => {
    this.setState(prevState => ({
      pageNumber: prevState.pageNumber + 1,
    }));
    window.scrollTo({
      top: document.documentElement.scrollHeight,
      behavior: 'smooth',
    });
  };

  handleOpenModal = ({ target }) => {
    this.setState({ isOpenModal: true, openImg: target.dataset.largeImg });
  };

  handleCloseModal = () => {
    this.setState({ isOpenModal: false });
  };

  render() {
    const { items, isLoading, isOpenModal, openImg } = this.state;
    const { id } = items;
    return (
      <div className={styles.app}>
        <SearchBar onSubmit={this.handleSearch} />
        {isLoading && <Loader />}
        {items.length && (
          <>
            <ImageGallery>
              {items.map(item => (
                <ImageGalleryItem
                  key={id}
                  item={item}
                  onClick={this.handleOpenModal}
                />
              ))}
            </ImageGallery>
            <Button onClick={this.handleLoadMore} />
          </>
        )}
        {isOpenModal && <Modal onClose={this.handleCloseModal} src={openImg} />}
      </div>
    );
  }
}

export default App;
